body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square { 
  border: 1px solid #999;
  float: left;
  height: 10px;
  width: 10px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
}
.square:focus {
  outline: none;
}
.emptySquare{
  background: #000;
}
.fillSquare{
  background: #FF0;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

body{
  background-color: #333;
}



